
.container{
    max-width: 1344px;
    margin: 0 auto;
    padding: 0 24px;
    @media (max-width:992px){
        max-width: 872px;
        margin: 0 auto;
    }
    @media (max-width:767px){
        max-width: 693px;
        margin: 0 auto;
    }
}

.header{
    background-color: #AA0601;
    .navbar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width:767px){
            flex-direction: column;
            align-items: center;
            }
    }
    .container{
        @media (max-width:767px){
            max-width: 100%;
            padding: 0;
        }   
    }
        img{
            width: 80px;
                padding: 12px 0;
            }
        .menu{
            display: flex;
            li{
                margin-left: 64px;
            }
            a{
                display: block;
                text-decoration: none;
                color: #FFFF;
                font-family:'Noto Sans TC', sans-serif ;
                font-size: 20px;
                line-height: 1.5;
                &:hover{
                    color:gray
                }
            }
        @media (max-width:767px){
            flex-wrap: wrap;
                li{ 
                    width: 50%;
                    margin: 0 auto;
                    border: 1px solid #FFFF;
                    a{
                        font-size: 16px;
                        text-align: center;
                        padding: 12px 100px;
                    }
                }
            }
            }
}